.navbar{
    width: 100%;
    position: fixed;
    top:0;
    z-index: 10;
    @media (min-width:768px) {
        display:none;
    }
}


.navbar1{
    width:100%;
    height: 60px;
    background: $color01trans;
    opacity:1;
    display:flex;
    justify-content: center;
    color:$color-light;
}
.navbar2{
    display:flex;
    justify-content: space-between;
    width:100%;
    background: $color02trans;
    color:$color-light;
}
.p{
    font-size:1rem;
    margin-left: 15px;
    margin-right: 15px;
    font-family: $font-type2;
}
.navbarml{
    width: 100%;
    position: fixed;
    top:0;
    z-index: 10;
    background: $color01;
    display:flex;
    justify-content: center;
    color:$color-light;
    display:flex;
    justify-content: space-between;
    @media (max-width:768px) {
        display:none;
    }
    @media (min-width:768px) {
opacity: 0.8;
    }
}

.navbarml div{
    display:flex;
    flex-direction:row;
    margin-right: 45px;
}
.navbarml h3{
    margin-left: 45px;
}